.wrapper{
	display: flex;
	flex-direction: column;
	gap: 16px;
	padding: 16px ;
	background-color: var(--background-sec);
   border-radius: 6px;
	button{
		color: var(--text-color);
		width: 100%;
		padding: 6px 12px;
        border-radius: 6px;
		font-size: 1rem;
		text-align: center;
		appearance: none;
		background-color: transparent;
		cursor: pointer;
		transition: all 500ms ease;
		&:hover{
			background-color: var(--background-sec);
			color: var(--LimeGreen);
		}
	}
}
.menu-wrapper {
	list-style: none;
	margin: 0;
	min-width: 180px;
	max-height: 70vh;
	padding: 4px 8px;
	box-shadow: rgb(0 0 0 / 12%) 0px 1px 6px;
	color: var(--text-color);
	border-radius: 6px;
	transition: all 0.5s ease-out;
	overflow: auto;
}
.menu-item {
	display: flex;
    justify-content: space-between;
    padding: 8px 0;
	gap: 6px;
	align-items: center;
	text-transform: capitalize;
	font-size: 0.75rem;
	font-weight: 300;
    font-family: sans-serif;
}
